import { useEffect, useState, useRef } from "react";
import TopNav from "../components/TopNav";

function Result() {
  const [result, setResult] = useState([]);
  const [score, setScore] = useState(0);
  const [feedback, setFeedback] = useState("");
  const printRef = useRef(); // Used to define printable area

  // Called on page load
  useEffect(() => {
    const stored = JSON.parse(localStorage.getItem("quizResult")) || [];
    setResult(stored);
    const correctCount = stored.filter(q => q.isCorrect).length;
    setScore(correctCount);
    setFeedback(generateFakeFeedback(correctCount));

    // Save record to quizHistory
    const history = JSON.parse(localStorage.getItem("quizHistory") || "[]");
    const newRecord = {
      timestamp: new Date().toISOString(),
      score: correctCount,
      total: stored.length,
      answers: stored,
    };
    localStorage.setItem("quizHistory", JSON.stringify([...history, newRecord]));
  }, []);

  // Simulated personalized feedback (no API)
  const generateFakeFeedback = (score) => {
    if (score < 8) {
      return "You might be new to death literacy topics. A good starting point is learning about Advance Care Planning.";
    } else if (score < 15) {
      return "You have a basic understanding of some death literacy areas. Keep exploring topics like Palliative Care and cultural perspectives.";
    } else if (score < 20) {
      return "You demonstrate a solid level of death literacy. Consider sharing this knowledge to support others.";
    } else {
      return "Outstanding! Your death literacy knowledge is exceptional. You could be a death literacy advocate.";
    }
  };

  // Export the result section to PDF using browser's print
  const handleExportPDF = () => {
    const content = printRef.current.innerHTML;
    const win = window.open("", "", "width=800,height=600");
    win.document.write(`
      <html><head><title>Quiz Result</title></head>
      <body>${content}</body></html>
    `);
    win.document.close();
    win.print();
  };

  return (
    <>
      <TopNav />
      <div style={{ padding: "2rem" }} ref={printRef}>
        <h2>Your Score: {score} / {result.length}</h2>
        <p><strong>🧠 Personalized Feedback:</strong></p>
        <p style={{ background: "#f2f2f2", padding: "1rem", borderRadius: "6px" }}>{feedback}</p>
        <ul>
          {result.map((q, i) => (
            <li key={i} style={{ marginBottom: "1rem" }}>
              <strong>Q{i + 1}: {q.question}</strong><br />
              ✅ Correct Answer: {q.correct.toUpperCase()} <br />
              📝 Your Answer: {q.selected.toUpperCase()} <br />
              {q.isCorrect ? "✔️ Correct" : "❌ Incorrect"}
            </li>
          ))}
        </ul>
      </div>

      <div style={{ padding: "2rem" }}>
        <button onClick={handleExportPDF}>📄 Download Result as PDF</button>
      </div>
    </>
  );
}

export default Result;
